import { useRouterContext } from '@speedlo/hooks'
import { Base64 } from 'js-base64'
import React from 'react'

import { useRoot } from '../app/useRoot'
import { DeliveryTypeEnum } from '../graph/generated'

/**
 * Side effects for handling url params coming from official portal.
 *
 * Two modes:
 *   ?pickup ... Show pickup map by default
 *   ?address=value ... Use given address as search value
 */
export function usePortalNavigation(isMobile = false) {
  const { history } = useRouterContext()
  const { order, location } = useRoot()

  const info = React.useMemo(() => {
    const url = new window.URL(window.location.href)
    const isPickup = url.searchParams.has('pickup')
    const address = url.searchParams.get('address')
    return {
      isPickup,
      searchAddress: address && decodeAddress(address),
    }
  }, [])

  React.useEffect(
    function handlePickup() {
      if (info.isPickup) {
        order.useOrderMethod(DeliveryTypeEnum.Pickup)
        if (isMobile) {
          history.push('/pickup')
        }
      }
    },
    [order, isMobile, history, info.isPickup],
  )

  React.useEffect(
    function handleDelivery() {
      if (info.searchAddress) {
        location.resetResolution()
        location.setInitialSearchAddress(info.searchAddress)
        if (isMobile) {
          history.push('/delivery')
        }
      }
    },
    [history, info.searchAddress, isMobile, location],
  )
}

function decodeAddress(encodedAddress: string) {
  try {
    return Base64.decode(encodedAddress)
  } catch {
    return null
  }
}
