import { useRoot } from '../../../app/useRoot'
import { useQOrderFinish } from '../../../graph/generated'

export function useFinishViewLoader() {
  const { order } = useRoot()

  const { data, loading } = useQOrderFinish(
    {
      branchId: order.branchId!,
      orderId: order.orderId!,
    },
    {
      skip: !(order.branchId && order.orderId),
    },
  )

  return loading || !data ? null : data
}
