import React from 'react'

import { useRoot } from '../app/useRoot'
import { useLazyQResolveLocation } from '../graph/generated'
import { GeoPoint } from '../helpers/gps'

export function useLocationResolver() {
  const { location, business } = useRoot()

  const [resolveLocation] = useLazyQResolveLocation({
    fetchPolicy: 'cache-first',
  })

  const resolver = React.useCallback(
    async (gps: GeoPoint) => {
      location.setIsResolving(true)

      const data = await resolveLocation({
        ...getResolutionTarget(),
        ...gps,
      })

      if (data !== null) {
        location.setResolutionResult(data.resolveLocation)
      }
      location.setIsResolving(false)

      function getResolutionTarget() {
        if (business.isBrand) {
          return {
            brandId: business.brandId!,
          }
        }
        return {
          branchId: business.branchId!,
        }
      }
    },
    [business, location, resolveLocation],
  )

  return resolver
}
