import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../app/useRoot'
import { useMChangeCartItem, useQCartItem } from '../../graph/generated'
import { ARRAY_EMPTY } from '../../helpers/constants'
import { RM } from '../../tools/ramda'
import { CartModal } from './CartModal'

interface IProps {
  orderId: MaybeID
}

export const ChangeCartModal = observer<IProps>(({ orderId }) => {
  const { order: orderModel, cart: cartModel } = useRoot()

  const itemId = orderModel.changeCartItemId

  const { getStash, loading } = useQCartItem(
    {
      itemId: itemId!,
    },
    { skip: orderId === null || itemId === null },
  )

  const [changeItem] = useMChangeCartItem()

  const onConfirm = React.useCallback(
    async sideDishes => {
      if (orderId === null || itemId === null) {
        return false
      }
      const result = await changeItem({
        itemId,
        sideDishes,
        orderId,
        withGoods: cartModel.withGoods,
      })
      return Boolean(result)
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [orderId, itemId, changeItem],
  )

  const onClose = React.useCallback(() => {
    orderModel.changeCartItem(null)
  }, [orderModel])

  const item = getStash(RM.prop('item'))

  const sideDishesSelection: RoA<ID> = item
    ? item.sideDishes.map(RM.path(['recipe', 'id']))
    : ARRAY_EMPTY

  return (
    <CartModal
      recipe={item ? item.recipe : null}
      initialSideDishes={sideDishesSelection}
      onConfirm={onConfirm}
      onClose={onClose}
      isLoading={loading}
    />
  )
})
